import axios from "axios";
import React, { Component, Fragment } from "react";
// import { FaRegFilePdf } from "react-icons/fa";
import { ImFilePdf } from "react-icons/im";
import {
  MdAttachFile,
  MdCheckBox,
  MdCheckBoxOutlineBlank,
  MdEdit,
  MdPersonAddDisabled,
} from "react-icons/md";
import { Auth } from "../../actions";
import { FC_GetDistricts } from "../../actions/transfer.action";
import { APP_URL, APP_URL_FILE } from "../../config/app.config";
import { DistrictTypes } from "../../types/TransferTypes";
import { errorToText } from "../../utils/errors";
import DashboardContainer from "../DashboardContainer/DashboardContainer";
import Loading from "../Loading/Loading";
import { FiDownload } from "react-icons/fi";
import Button from "../Buttons/Button";
// import { MdHearingDisabled } from "react-icons/md";

export interface UserDocumentsInterface {
  resident_district_id: string | null;
  disability: string | null;
  disability_surpoting_document: string | null;
  resident_surpoting_document: string | null;
}

const validateFile = (file: File): string => {
  let error = "";
  if (file.size === 0) {
    error = "Invalid file";
  }
  if (file.size > 1038221) {
    // 1MB"jpg",
    error = "File should not be greater than 1MB";
  }
  if (
    file.type !== "application/pdf" &&
    file.type !== "image/png" &&
    file.type !== "image/jpeg" &&
    file.type !== "image/webp" &&
    file.type !== "image/tiff" &&
    file.type !== "image/bmp" &&
    file.type !== "image/gif" &&
    file.type !== "image/jfif" &&
    file.type !==
      "application/vnd.openxmlformats-officedocument.wordprocessingml.document"
  ) {
    error =
      "Invalid file type, accepted files(PDF, IMAGES(png, jpeg, webp, tiff, bmp, gif, jfif))";
  }
  // console.log("File details: ", file.type);
  if (error !== "") {
    return error;
  }
  return "";
};

enum DOC_TYPE_ADD {
  RESIDENCE = "RESIDENCE",
  DISABILITY = "DISABILITY",
  ALL = "ALL",
}

interface UserDocumentsProps {
  auth: Auth;
  FC_UpdateUserDocuments: (data: UserDocumentsInterface) => void;
}
interface UserDocumentsState {
  districts: DistrictTypes[] | null;
  loading: boolean;
  loading_saving: boolean;
  addDocuments: boolean;
  addDocType: DOC_TYPE_ADD;
  resident_district_id: string | null;
  disability_surpoting_document: File | null;
  resident_surpoting_document: File | null;
  error: {
    element: string;
    msg: string;
  } | null;
  success: string;
  disabled_none_selected: boolean;
}

export class UserDocuments extends Component<
  UserDocumentsProps,
  UserDocumentsState
> {
  constructor(props: UserDocumentsProps) {
    super(props);

    this.state = {
      districts: null,
      loading: false,
      loading_saving: false,
      addDocuments: false,
      addDocType: DOC_TYPE_ADD.ALL,
      resident_district_id: null,
      disability_surpoting_document: null,
      resident_surpoting_document: null,
      error: null,
      success: "",
      disabled_none_selected: false,
    };
  }

  setLoadingSaving = (status: boolean) =>
    this.setState({ loading_saving: status });

  UpdateUserDocuments = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    this.setState({
      error: null,
      success: "",
    });
    if (this.state.addDocType === DOC_TYPE_ADD.RESIDENCE) {
      if (
        this.state.resident_district_id === null ||
        this.state.resident_district_id === ""
      ) {
        return this.setState({
          error: {
            element: "main",
            msg: "Please select residence district!",
          },
        });
      }
      if (this.state.resident_surpoting_document === null) {
        return this.setState({
          error: {
            element: "resident_surpoting_document",
            msg: "Please select resident surpoting document!",
          },
        });
      } else {
        if (validateFile(this.state.resident_surpoting_document) !== "") {
          return this.setState({
            error: {
              element: "main",
              msg: validateFile(this.state.resident_surpoting_document),
            },
          });
        }
      }
    }
    if (this.state.addDocType === DOC_TYPE_ADD.DISABILITY) {
      if (this.state.disability_surpoting_document === null) {
        return this.setState({
          error: {
            element: "disability_surpoting_document",
            msg: "Please select disability surpoting document!",
          },
        });
      } else {
        if (validateFile(this.state.disability_surpoting_document) !== "") {
          return this.setState({
            error: {
              element: "main",
              msg: validateFile(this.state.disability_surpoting_document),
            },
          });
        }
      }
    }

    const formData = new FormData();
    formData.append(
      "disability",
      (this.state.addDocType === DOC_TYPE_ADD.DISABILITY ? true : "").toString()
    );
    formData.append(
      "resident_district_id",
      this.state.resident_district_id === null
        ? ""
        : this.state.resident_district_id
    );
    formData.append(
      "disability_surpoting_document",
      this.state.disability_surpoting_document === null
        ? ""
        : this.state.disability_surpoting_document
    );
    formData.append(
      "resident_surpoting_document",
      this.state.resident_surpoting_document === null
        ? ""
        : this.state.resident_surpoting_document
    );
    const resident_district_name = this.state.districts?.find(
      (itm) => itm.district_code === this.state.resident_district_id
    );
    formData.append(
      "resident_district_name",
      resident_district_name === undefined
        ? ""
        : resident_district_name.district_name
    );

    // console.log("FormData: ", formData.values());
    this.setState({ loading_saving: true });
    try {
      const res = await axios.post(
        `${APP_URL}/user/account/updateprofile`,
        formData
      );
      this.setState({
        success: "Document added successfully!",
      });
      setInterval(() => {
        if (this.state.loading_saving === true) {
          this.props.FC_UpdateUserDocuments({
            resident_district_id: this.state.resident_district_id,
            disability: (this.state.addDocType === DOC_TYPE_ADD.DISABILITY
              ? true
              : false
            ).toString(),
            disability_surpoting_document: res.data.disabilityNewFileName,
            resident_surpoting_document: res.data.residentNewFileName,
          });
          this.setState({
            loading_saving: false,
          });
        }
      }, 3000);
      // console.log("Success: ", res.data);
      // console.log("Success test: ", {
      //   resident_district_id: this.state.resident_district_id,
      //   disability: (this.state.addDocType === DOC_TYPE_ADD.DISABILITY
      //     ? true
      //     : false
      //   ).toString(),
      //   disability_surpoting_document: res.data.disabilityNewFileName,
      //   resident_surpoting_document: res.data.residentNewFileName,
      // });
    } catch (error: any) {
      this.setState({
        error: {
          element: "main",
          msg: errorToText(error),
        },
        loading_saving: false,
      });
      // console.log("error: ", { ...error });
    }
  };

  SubmitNoDisability = async () => {
    this.setState({ loading_saving: true });
    try {
      const resident_district_id = this.props.auth.user?.resident_district_id;
      const disability_surpoting_document =
        this.props.auth.user?.disability_surpoting_document;
      const resident_surpoting_document =
        this.props.auth.user?.resident_surpoting_document;
      const resident_district_name =
        this.props.auth.user?.resident_district_name;
      const res = await axios.post(`${APP_URL}/user/account/updateprofile`, {
        disability: false.toString(),
        resident_district_id:
          resident_district_id === null || resident_district_id === undefined
            ? ""
            : resident_district_id,
        disability_surpoting_document:
          disability_surpoting_document === null ||
          disability_surpoting_document === undefined
            ? ""
            : disability_surpoting_document,
        resident_surpoting_document:
          resident_surpoting_document === null ||
          resident_surpoting_document === undefined
            ? ""
            : resident_surpoting_document,
        resident_district_name:
          resident_district_name === null ||
          resident_district_name === undefined
            ? ""
            : resident_district_name,
      });
      this.setState({
        success: "Submitted successfully!",
      });
      setInterval(() => {
        this.props.FC_UpdateUserDocuments({
          resident_district_id:
            resident_district_id === null || resident_district_id === undefined
              ? ""
              : resident_district_id,
          disability: false.toString(),
          disability_surpoting_document: res.data.disabilityNewFileName,
          resident_surpoting_document: res.data.residentNewFileName,
        });
        this.setState({
          loading_saving: false,
        });
      }, 3000);
      alert("Submitted successfully!");
      // console.log("Success: ", res.data);
    } catch (error: any) {
      this.setState({
        error: {
          element: "main",
          msg: errorToText(error),
        },
        loading_saving: false,
      });
      // console.log("error: ", { ...error });
    }
  };

  componentDidMount = () => {
    if (this.state.districts === null) {
      this.setState({ loading: true });
      FC_GetDistricts(
        (status: boolean, data: DistrictTypes[] | null, msg: string) => {
          if (data !== null) {
            this.setState({
              districts: data,
              loading: false,
            });
            // console.log(data);
          }
          this.setState({ loading: false });
        }
      );
    }
  };
  render() {
    if (this.state.districts === null || this.state.loading === true) {
      return (
        <DashboardContainer>
          <div className="flex flex-col items-center justify-center gap-3 w-full text-center py-5">
            <Loading />
            <div>Loading...</div>
          </div>
        </DashboardContainer>
      );
    }
    return (
      <Fragment>
        {this.state.success !== "" && (
          <div className="text-green-700 bg-green-100 rounded px-3 py-2">
            {this.state.success}
          </div>
        )}
        {this.state.error !== null && (
          <div className="text-red-700 bg-red-100 rounded px-3 py-2">
            {this.state.error.msg}
          </div>
        )}
        {this.state.addDocuments === false ? (
          <div>
            <div className="py-2 border-b">
              <span className="text-xl font-bold ">My documents</span>
            </div>
            <div className="py-4 grid grid-cols-1 md:grid-cols-2 gap-4">
              <div
                className={`col-span-1 flex items-center gap-3 ${
                  this.props.auth.user?.resident_surpoting_document === null ||
                  this.props.auth.user?.resident_surpoting_document === ""
                    ? "bg-yellow-100"
                    : "bg-gray-100"
                } rounded-lg p-3 py-4 hover:bg-blue-100 hover:text-primary-800 group`}
              >
                <div>
                  {this.props.auth.user?.resident_surpoting_document === null ||
                  this.props.auth.user?.resident_surpoting_document === "" ? (
                    <MdAttachFile className="text-gray-400 group-hover:text-primary-800 text-6xl" />
                  ) : (
                    <ImFilePdf className="text-primary-800 group-hover:text-primary-800 text-6xl" />
                  )}
                </div>
                <div>
                  {this.props.auth.user?.resident_surpoting_document === null ||
                  this.props.auth.user?.resident_surpoting_document === "" ? (
                    <div className="text-accent-900 font-bold group-hover:text-primary-800">
                      Resident district is missing!
                    </div>
                  ) : (
                    <div className="text-gray-900 font-bold group-hover:text-primary-800">
                      My resident district
                    </div>
                  )}
                  <div
                    onClick={() => {
                      (this.props.auth.user?.resident_surpoting_document ===
                        null ||
                        this.props.auth.user?.resident_surpoting_document ===
                          undefined ||
                        this.props.auth.user?.resident_surpoting_document ===
                          "") &&
                        this.setState({
                          addDocuments: true,
                          addDocType: DOC_TYPE_ADD.RESIDENCE,
                        });
                    }}
                    className="text-xl font-bold cursor-pointer"
                  >
                    {this.props.auth.user?.resident_surpoting_document ===
                      null ||
                    this.props.auth.user?.resident_surpoting_document === "" ? (
                      <span className="underline">Choose your residence</span>
                    ) : (
                      <span className="text-primary-800">
                        {
                          this.state.districts.find(
                            (itm) =>
                              itm.district_code ===
                              this.props.auth.user?.resident_district_id
                          )?.district_name
                        }
                      </span>
                    )}
                  </div>
                  {this.props.auth.user?.resident_surpoting_document !== null &&
                    this.props.auth.user?.resident_surpoting_document !==
                      undefined &&
                    this.props.auth.user?.resident_surpoting_document !==
                      "" && (
                      <div className="flex flex-row items-center w-full gap-2 mt-2">
                        <div
                          onClick={() => {
                            this.setState({
                              addDocuments: true,
                              addDocType: DOC_TYPE_ADD.RESIDENCE,
                            });
                          }}
                          className="bg-white cursor-pointer rounded flex items-center gap-2 px-2 py-1 hover:bg-primary-800 hover:text-white"
                        >
                          <div>
                            <MdEdit className="text-lg" />
                          </div>

                          <span>Update</span>
                        </div>
                        <a
                          href={`${APP_URL_FILE}/${this.props.auth.user.resident_surpoting_document}`}
                          target="__blank"
                          className="bg-white cursor-pointer rounded flex items-center gap-2 px-2 py-1 hover:bg-primary-800 hover:text-white"
                        >
                          <div>
                            <FiDownload className="text-lg" />
                          </div>

                          <span>View</span>
                        </a>
                      </div>
                    )}
                </div>
              </div>

              <div className="col-span-1 flex items-center gap-3 bg-gray-50 rounded-lg p-3 py-4 hover:bg-gray-100  group">
                <div>
                  {this.props.auth.user?.disability_surpoting_document ===
                    null ||
                  this.props.auth.user?.disability_surpoting_document === "" ? (
                    <MdPersonAddDisabled className="text-gray-400  text-6xl" />
                  ) : (
                    <ImFilePdf className="text-primary-800 group-hover:text-primary-800 text-6xl" />
                  )}
                </div>

                <div>
                  {!this.props.auth.user?.disability_surpoting_document && (
                    <div className="text-gray-600 ">
                      Do you have a disability?
                    </div>
                  )}
                  {this.props.auth.user?.disability_surpoting_document && (
                    <div className="text-gray-600 ">
                      <h2 className="text-lg font-bold cursor-pointer text-black">
                        Disability document is found.
                      </h2>
                      <span className="text-sm">
                        you can Change disability document
                      </span>
                    </div>
                  )}
                  <div className="text-xl font-bold cursor-pointer">
                    {this.props.auth.user?.disability_surpoting_document ===
                      null ||
                    this.props.auth.user?.disability_surpoting_document ===
                      "" ? (
                      <div className="flex flex-row items-center gap-3 text-base">
                        <Button
                          onClick={() => {
                            (this.props.auth.user
                              ?.disability_surpoting_document === null ||
                              this.props.auth.user
                                ?.disability_surpoting_document === undefined ||
                              this.props.auth.user
                                ?.disability_surpoting_document === "") &&
                              this.setState({
                                addDocuments: true,
                                addDocType: DOC_TYPE_ADD.DISABILITY,
                                disabled_none_selected: false,
                              });
                          }}
                          type="button"
                          theme={
                            this.state.disabled_none_selected === true
                              ? "default"
                              : "accent-light"
                          }
                        >
                          <b>Yes</b>, I have s disability
                        </Button>
                        <Button
                          type="button"
                          className="px-2"
                          theme={
                            this.state.disabled_none_selected === true
                              ? "success"
                              : "accent-light"
                          }
                          onClick={() =>
                            this.setState({ disabled_none_selected: true })
                          }
                        >
                          {this.state.disabled_none_selected === true ? (
                            <MdCheckBox className="inline-block text-xl mr-1" />
                          ) : (
                            <MdCheckBoxOutlineBlank className="inline-block text-xl mr-1" />
                          )}
                          No
                        </Button>
                        {/* <div
                            onClick={() => {
                              // this.SubmitNoDisability();
                              this.setState({
                                loading_saving: true,
                              });
                              setInterval(() => {
                                this.setState({
                                  success: "Submitted successfully!",
                                  loading_saving: false,
                                });
                              }, 3000);
                              this.setState({ addDocuments: false });
                            }}
                            className="bg-gray-200 group-hover:bg-white rounded px-3 border border-gray-200 group-hover:border-accent-900 group-hover:text-accent-900"
                          >
                            No
                          </div> */}
                      </div>
                    ) : (
                      ""
                    )}
                  </div>

                  {this.props.auth.user?.disability_surpoting_document !==
                    null &&
                    this.props.auth.user?.disability_surpoting_document !==
                      undefined &&
                    this.props.auth.user?.disability_surpoting_document !==
                      "" && (
                      <div className="flex flex-row items-center w-full gap-2 mt-2">
                        <div
                          onClick={() => {
                            this.setState({
                              addDocuments: true,
                              addDocType: DOC_TYPE_ADD.DISABILITY,
                            });
                          }}
                          className="bg-white cursor-pointer rounded flex items-center gap-2 px-2 py-1 hover:bg-primary-800 hover:text-white"
                        >
                          <div>
                            <MdEdit className="text-lg" />
                          </div>

                          <span>Update</span>
                        </div>
                        <a
                          href={`${APP_URL_FILE}/${this.props.auth.user.disability_surpoting_document}`}
                          target="__blank"
                          className="bg-white cursor-pointer rounded flex items-center gap-2 px-2 py-1 hover:bg-primary-800 hover:text-white"
                        >
                          <div>
                            <FiDownload className="text-lg" />
                          </div>

                          <span>View</span>
                        </a>
                      </div>
                    )}
                </div>
              </div>
            </div>
          </div>
        ) : (
          <div className="p-5 bg-white animate__animated animate__fadeIn animate__faster">
            <form
              onSubmit={this.UpdateUserDocuments}
              className="flex flex-col md:flex-row items-center gap-6"
            >
              <div className="flex flex-col text-base">
                {this.state.addDocType === DOC_TYPE_ADD.RESIDENCE && (
                  <>
                    <label className="font-bold">
                      Choose resident district
                    </label>
                    <select
                      value={
                        this.state.resident_district_id === null
                          ? ""
                          : this.state.resident_district_id
                      }
                      onChange={(e) =>
                        this.setState({ resident_district_id: e.target.value })
                      }
                      className="border border-gray-400 rounded px-3 py-2 mb-5"
                    >
                      <option value="">Choose district</option>
                      {this.state.districts.map((item, i) => (
                        <option key={i + 1} value={item.district_code}>
                          {item.district_name}
                        </option>
                      ))}
                    </select>
                    <label className="font-bold">
                      Residence supporting document {"(Size < 1MB)"}
                    </label>
                    <input
                      type="file"
                      onChange={(e) =>
                        this.setState({
                          resident_surpoting_document:
                            e.target.files === null ? null : e.target.files[0],
                        })
                      }
                      className="border border-gray-400 rounded px-3 py-2"
                    />
                  </>
                )}
              </div>
              <div className="flex flex-col text-base">
                {this.state.addDocType === DOC_TYPE_ADD.DISABILITY && (
                  <>
                    <label className="font-bold">
                      Disability supporting document {"(Size < 1MB)"}
                    </label>
                    <input
                      type="file"
                      onChange={(e) =>
                        this.setState({
                          disability_surpoting_document:
                            e.target.files === null ? null : e.target.files[0],
                        })
                      }
                      className="border border-gray-400 rounded px-3 py-2"
                    />
                  </>
                )}
                {/* Warning */}
                <div className="px-3 py-2 rounded bg-yellow-100 gap-3">
                  <span className="font-extrabold">Warning</span> Make sure you
                  are providing correct information. For any inconvenience,
                  rules will be applied
                </div>
                {/* {this.state.loading_saving === false ? ( */}
                <div className="flex items-center gap-3 mt-6">
                  <Button
                    onClick={() =>
                      this.setState({
                        addDocuments: false,
                        addDocType: DOC_TYPE_ADD.ALL,
                        resident_district_id: null,
                        disability_surpoting_document: null,
                        resident_surpoting_document: null,
                        success: "",
                        error: null,
                      })
                    }
                    theme="default"
                  >
                    Back
                  </Button>
                  <Button type="submit" theme="success">
                    Submit
                  </Button>
                </div>
                {/* ) : (
                  <div className="text-accent-900">Loading, please wait...</div>
                )} */}
              </div>
            </form>
          </div>
        )}
      </Fragment>
    );
  }
}

export default UserDocuments;
